import { HttpParams } from '@angular/common/http';
import { ApiService } from './api.service';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CommonService {

  constructor(public api: ApiService) { }

  // rtc token
  async generateTokenAndUid(uid) {
    // https://test-agora.herokuapp.com/access_token?channel=test&uid=1234
    let url = 'http://localhost:8081/rtcToken';
    const opts = { params: new HttpParams({ fromString: "channelName=test&uid=" + uid }) };
    const data = await this.api.getRequest(url,opts.params).toPromise();
    return { 'uid': uid, token: data }

  }

  generateUid() {
    const length = 5;
    const randomNo = (Math.floor(Math.pow(10, length - 1) + Math.random() * 9 * Math.pow(10, length - 1)));
    return randomNo;
  }
}
